body {
  background-color: var(--mat-sys-surface);
  color: var(--mat-sys-on-surface);
  font: var(--mat-sys-body-medium);
  margin: 0;
}

/* Global animation disable utility class for testing/bootstrapping */
.no-animation,
.no-animation * {
  animation: none !important;
  transition: none !important;
}

div {
  border-width: 0;
  border-style: solid;
}

/* Override for Material Design components */
[class*=mat-] {
  border: initial;
}

/* You can add global styles to this file, and also import other style files */
.appear {
  animation: appear 0.8s ease-in;
}

@keyframes appear {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
/* Import ng-select theme for consistent styling */
:root {
  --color-primary: #7c3aed;
  --color-primary-gradient: #a871db;
  --color-secondary: #9333ea;
  --color-bg: #ffffff;
  --color-text: #0f172a;
  --color-text-on-primary: #ffffff;
}

:root[data-theme=dark] {
  --color-primary: #7c3aed;
  --color-primary-gradient: #a871db;
  --color-secondary: #9333ea;
  --color-bg: #0b1220;
  --color-text: #e6eef8;
  --color-text-on-primary: #ffffff;
}

@tailwind base;
@tailwind components;
@tailwind utilities;
html,
body {
  height: 100%;
}

body {
  margin: 0;
  font-family: Roboto, "Helvetica Neue", sans-serif;
